body,
html {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: black ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.homepagemain{
  position: absolute;
  top: 50px;
  bottom: 20px;
  width: 100%;
}

.mainfooter{
  position: absolute;
  height: 20px;
  bottom: 0px;
  width: 100%;
  font-size: 8px;
}

.col-width90{
  width: 90%;
}

.mainboxcontainer {
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  grid-template-columns:  repeat(auto-fill, minmax(75px, 250px));
  /*grid-template-rows: auto;*/
  padding: 1rem;
  margin: 2rem;
  align-content: center;
  justify-content: center;
}

.mainbox {
  display: grid;
  cursor: pointer;
  width: 100%;
  color: white;
}

.mainbox::before {
  display: grid;
  content:  "";
  padding-top: 100%;
  display: block;
  background: lightslategrey;
  grid-area: 1 / 1 / 2 / 2;
}

.mainbox > h3 {
  width: auto;
  margin: auto;
  grid-area: 1 / 1 / 2 / 2;
  align-self: center;
  justify-self: center;
}

.mainboxscroll {
  /*display: none;*/
  overflow-y: scroll;
  border: 2px solid white;
  background: lightslategrey;
}

